import React, {useState} from 'react';
import {NavLink} from 'react-router-dom'

function AppHeader() {
  const [menuBtn, setMenuBtn] = useState(false);

  const onMenuChange = ()=>{
    if(window.innerWidth < 767){
      setMenuBtn(!menuBtn)
    }
  }
  return (
    <header>
      <div className="logo">
        <img src={require('../../images/weblogo.png')} alt="logo"/>
      </div>
      <div className="menu">
        <i className={`material-icons ${menuBtn ? 'active' : ''}`} onClick={()=>setMenuBtn(!menuBtn)} >{menuBtn ? 'close' : 'menu'}</i>
        <nav className={menuBtn ? 'active' : ''} onClick={onMenuChange}>
          <NavLink exact to={'/'}>Home</NavLink>
          <NavLink to={'/portfolio'}>Portfolio</NavLink>
          <NavLink to={'/about'}>About Us</NavLink>
          <NavLink to={'/contact'}>Contact</NavLink>
        </nav>
      </div>
    </header>
  );
}

export default AppHeader;
